#project
	display: block

	h1
		font:
			weight: 500
			size: 40px
		line-height: 1.5

	@media screen and (max-width: 460px)
		h1
			font:
				size: 36px
			line-height: 1.25						

	.project-cover
		margin-bottom: 40px

		.case-title
			font:
				family: 'Inter', 'Roboto', sans-serif
				size: 18px
				weight: 500
			color: $prussian-blue			

		.case-desc
			margin-top: 16px

		.data-blocks
			display: grid
			grid-template-columns: repeat(12,1fr)
			grid-gap: 2em
			margin-top: 32px

			.meta-block
				grid-column: auto/span 4

			@media screen and (max-width: 460px)
				.meta-block
					grid-column: auto/span 10 	

	hr
		width: 2vw
		border: 1px solid $greyish								

	.case-study
		display: grid

		.section
			margin-top: 5em

		img
			height: auto
			max-width: 100%
			display: block
			margin-bottom: 40px

		.full-width
			width: 100vw
			position: relative
			left: 50%
			right: 50%
			margin-left: -50vw
			margin-right: -50vw
			max-width: none

		h4
			font:
				family: 'Inter', 'Roboto', sans-serif
				size: 18px
				weight: 500
			color: $prussian-blue

		h2
			line-height: 1.5
			font:
				weight: 500	
			margin-top: 0px	

		p
			margin-bottom: 1.5em

		ol
			margin-top: -1em

		span.caption
			color: $soft-gray
			font:
				size: .875em
			line-height: 20%

		.return
			margin-top: 80px
			text-align: center
			text-decoration: underline		