@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,400i,500,700&display=swap');

@import url('https://fonts.googleapis.com/css?family=Literata:400,500,600,700&display=swap');

@import url('https://rsms.me/inter/inter.css');
html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400, 600, 700, 800;
  src: local('Open Sans'), local('OpenSans'), url(//themes.googleusercontent.com/static/fonts/opensans/v8/cJZKeOuBrn4kERxqtaUH3bO3LdcAZYWl9Si6vvxL-qU.woff) format('woff');
}