header
	padding: 48px
	margin-bottom: 80px

	.logo
		float: left

	@media screen and (max-width: 640px)
		.logo
			float: none
			display: block
			text-align: center

	nav
		float: right

		a
			display: inline-block
			margin-left: 64px
			color: $soft-gray
			font:
				size: 16px
				weight: 400

		a:hover
			color: darken($soft-gray, 25%)

		.active
			border-bottom: 2px solid $greyish
			padding-bottom: 8px
			color: $text-color
			font:
				weight: 600	

		@media screen and (max-width: 640px)
			a
				margin: 16px 24px

	@media screen and (max-width: 640px)			
		nav
			float: none
			text-align: center

@media screen and (max-width: 640px)			
	header
		margin-bottom: 0
		padding: 24px				