#about
    margin: 0 auto
    max-width: 800px
    padding: 0 48px

    h1
        margin-top: 0px

    h4
        margin-bottom: 16px
        margin-top: 32px        

    .main-about
        width: 80%
        display: inline-block
        padding-right: 24px

        img
            width: 100%
            height: auto
            display: block

        a
            text-decoration: underline

    #sidebar
        width: 15%
        float: right

        li
            list-style-type: none
            margin-bottom: 8px

        ul
            padding-inline-start: 0px
            margin: 8px auto        		

@media screen and (max-width: 1280px)
    #about

        #sidebar
            float: none
            display: block

        .main-about
            width: 100%

        .datahead
            margin-top: 40px

@media screen and (max-width: 460px)
    #about
        padding: 0 24px   