//	Typography

.gigantic, .huge, .large, .bigger, .big, 
h1, h2, h3, h4, h5, h6
  color: $accent-color
  font: 
    family: 'Literata'

.gigantic
  font-size: 110px
  line-height: 1.09
  letter-spacing: -2px

.huge, h1
  font:
    size: 40px
    weight: 700
  line-height: 1.05
  letter-spacing: -1px

.large, h2
  font:
    size: 28px
  line-height: 1.14

@media screen and (max-width: 460px)
  h1
    font:
      size: 36px

  h2
    font:
      size: 28px    

.bigger, h3 
  font-size: 24px
  line-height: 1.38

.big, h4
  font:
    size: 20px
    weight: 400
  line-height: 1.38

.small, small 
  font-size: 10px
  line-height: 1.2
  color: green

// Typography

p
  margin: 0 0 20px 0

em
  font-style: italic

strong
  font-weight: bold

hr
  border: solid #ddd
  border-width: 1px 0 0
  clear: both
  margin: 10px 0 30px
  height: 0

.muted
  color: $soft-gray  

// Buttons

.button,
button,
input[type="submit"],
input[type="reset"],
input[type="button"]
  display: inline-block
  height: 38px
  padding: 0 30px
  color: #555
  text-align: center
  font-size: 11px
  font-weight: 600
  font-family: 'Roboto'
  line-height: 38px
  letter-spacing: .1rem
  text-transform: uppercase
  text-decoration: none
  white-space: nowrap
  background-color: transparent
  border-radius: 2px
  border: 1px solid #bbb
  cursor: pointer
  box-sizing: border-box

  &:hover,
  &:active,
  &:focus
    color: #333
    border-color: #888
    outline: 0

// Links

a
  color: $link-color
  text-decoration: none
  outline: 0
  
a:hover, a:focus
  color: darken($link-color, 15%)

// Lists

li
  margin-bottom: 8px 


// Selection colors (easy to forget)

::selection
  background: $selection-color
  
::-moz-selection
  background: $selection-color
  
img::selection
  background: transparent
  
img::-moz-selection
  background: transparent
  
body
  -webkit-tap-highlight-color: $selection-color

// Basic Styles

body
  background: $bg-color
  font:
    family: 'Inter', 'Open Sans', 'Roboto', sans-serif
  line-height: 1.8
  color: $text-color
  -webkit-font-smoothing: antialiased
  -webkit-text-size-adjust: 100%

  .wrapper
    max-width: 800px
    margin-right: auto
    margin-left: auto
    padding-left: 48px
    padding-right: 48px

  @media screen and (max-width: 460px)    
    .wrapper
      padding-right: 24px
      padding-left: 24px      

// Global Custom

.datahead
  display: block
  font:
    size: .75em
    weight: 500
  line-height: 1
  margin-bottom: 8px
  color: rgba(0,0,0,0.8)
  text-transform: uppercase

// Spacing utilities

.mt-1
  margin-top: 1em

.mt-2
  margin-top: 1em * 2

.mt-4
  margin-top: 1em * 4

  

.mb-2
  margin-bottom: 32px

.mb-3
  margin-bottom: 48px  

.mb-4
  margin-bottom: 64px

.mb-6
  margin-bottom: 96px
