#home 

  .wrapper
    display: grid
    grid-template-columns: repeat(12,1fr)
    grid-gap: 2em
    margin-right: auto
    margin-left: auto
    max-width: 800px
    padding-right: 48px
    padding-left: 48px    

    .work-block
      grid-column: auto/span 12
      margin-bottom: 56px

      a

        img
          max-width: 100% 
          height: auto
          border-radius: 2px

        .work-desc
          padding-top: 8px

          .work-title
            display: block
            font:
              weight: 600
              size: 15px
            color: rgba(0,0,0, 0.8)
            letter-spacing: 0.2px
            text-transform: uppercase 

          .work-copy
            display: block
            font:
              weight: 300
              size: 16px
            color: $text-color

    .callout
      grid-column: auto/span 11     

      h1
        margin-bottom: 0px
        margin-top: 0px
        color: rgba(0,0,0,0.8)

      h2
        font:
          weight: 300
        margin-top: 16px
        line-height: 1.5

    hr
      grid-column: auto/span 12          

    @media screen and (max-width: 960px)    
      .work-block
        grid-column: auto/span 12  

      .callout
        grid-column: auto/span 12

  @media screen and (max-width: 460px)    
    .wrapper
      grid-gap: 1em
      padding-right: 24px
      padding-left: 24px           